// Logo Ondernemersplein

.logo {
	display: block;
	position: relative;
	//margin: 0.5rem 1rem;

	&-link {
		@include noUnderline;

		font-weight: bold;
		color: getColor(grijs darkest);
		display: inline-block;
		font-size: 32px;
		line-height: .85;
		height: 55px;

		&:hover, &:focus, &:active {
			color: getColor(hemelblauw default);

			&:before {
				content: "Home";
				display: block;
				position: absolute;
				bottom: 6px;
				left: 90px;
				z-index: 1;
				font-size: 20px;
				font-weight: normal;
			}
		}
		&:focus {
			outline: 1px dotted getColor(grijs darkest);
		}
	}
}