/* BREADCRUMBS */

.breadcrumbs {
	margin: 0 0.5rem;
	padding-left: 0;

	li {
		display: inline-block;
		.brandBar & {
			color: getColor(wit);
		}
		&:before {
			content: ">";
			display: inline-block;
			padding-right: $pad;
		}
		&:first-child:before {
			content: "";
			padding-right: 0;
		}
	}

	a {
		@include noUnderline;
		display: inline-block;
		padding: $pad-half $pad $pad-half 0;

		.brandBar & {
			color: getColor(wit);
		}

		&:hover, &:active, &:focus {
			@include underline;
		}
	}
}